.select {
  box-sizing: border-box;
  width: 100%;
  height: 40px;
  padding: 0 1rem;
  border-radius: 15px;
  box-shadow: 0px 4px 24px -1px rgba(0, 0, 0, 0.1);
  font-size: 16px;
  border: none;
  color: #3e6ff0;
  background: linear-gradient(
    108.1deg,
    rgba(255, 255, 255, 0.4) 12.41%,
    rgba(254, 254, 254, 0.1) 88.76%
  );
  @media screen and(min-width: 767px) {
    width: 180px;
    height: 50px;
    border-radius: 25px;
    padding: 0.5rem 1rem;
  }
  @media screen and(min-width: 1024px) {
    width: 280px;
    height: 80px;
    border-radius: 25px;
    padding: 1rem;
    font-size: 25px;
  }
}
.full_width {
  width: 100%;
}
